import { Map, List, fromJS } from "immutable"

const initialState = Map ({
  items: List(),
  isLoading: false,
  singleItem: Map()
})

initialState.set("isLoading",true);

console.log("initialState:",initialState);

const reducer = ( state = initialState, action ) => {
  // console.log("blog-reducer-action:",action);
  // console.log("blog-reducer-state:",state);

  switch (action.type){
    case window.actions.article.GET_ARTICLE:
      return state.set( "items", fromJS( action.items ) )

    case window.actions.article.GET_ITEM_ARTICLE:
      return state.set( "singleItem", fromJS( action.singleItem ) )

    case window.actions.article.LOADING_ARTICLE:
      return state.set("isLoading", true)

    case window.actions.article.LOADED_ARTICLE:
      return state.set("isLoading", false)

    case window.actions.article.DELETE_ARTICLE_SUCCESS:
      let items = state.get("items").toJS().filter ( item => {
        item.id !== action.deletedId
      })
      return state.set("items", fromJS (items ))
      break

    case window.actions.article.UPDATE_ARTICLE_SUCCESS:
      let _items = state.get("items").toJS()
      let findedIndex = _items.findIndex( item => { return item.id === action.updated.id })

      findedIndex !== -1 && Object.assign( _items[ findedIndex ], action.updated )
      let _state = state.set( "items", fromJS( _items ) )
      return _state.set("singleItem", fromJS(action.updated) )

    default:
      return state
  }

}

export default reducer



