window.apiUrl = window.apiUrl || {}

const apiHost = process.env.NODE_ENV === "development" ? "http://0.0.0.0:3000" : "http://localhost:6900"

window.apiUrl.blogs = `${apiHost}/api/blogs`
window.apiUrl.users = `${apiHost}/api/users`
window.apiUrl.articles = `${apiHost}/api/articles`



