import fetch from "isomorphic-fetch"
import BaseAction from "./baseAction"

export const GET_ARTICLE = "GET_ARTICLE"
export const POST_ARTICLE = "POST_ARTICLE"
export const GET_ITEM_ARTICLE = "GET_ITEM_ARTICLE"
export const LOADING_ARTICLE = "LOADING_ARTICLE"
export const LOADED_ARTICLE = "LOADED_ARTICLE"
export const CREATE_ARTICLE_SUCCESS = "CREATE_ARTICLE_SUCCESS"
export const CREATE_ARTICLE_FAILURE = "CREATE_ARTICLE_FAILURE"
export const DELETE_ARTICLE_SUCCESS = "DELETE_ARTICLE_SUCCESS"
export const DELETE_ARTICLE_FAILURE = "DELETE_ARTICLE_FAILURE"
export const UPDATE_ARTICLE_SUCCESS = "UPDATE_ARTICLE_SUCCESS"
export const UPDATE_ARTICLE_FAILURE = "UPDATE_ARTICLE_FAILURE"

class articleAction extends BaseAction{
  constructor( baseUrl, name ){
    super( baseUrl, name )
    
    this.loadingMessage = LOADING_ARTICLE
    this.loadedMessage = LOADED_ARTICLE
    this.getDataMessage = GET_ARTICLE
    this.getItemMessage = GET_ITEM_ARTICLE

    this.createItemSuccessMesage = CREATE_ARTICLE_SUCCESS
    this.createItemFalureMessage = CREATE_ARTICLE_FAILURE

    this.deleteItemSuccessMesage = DELETE_ARTICLE_SUCCESS
    this.deleteItemFalureMessage = DELETE_ARTICLE_FAILURE

    this.updateItemSuccessMesage = UPDATE_ARTICLE_SUCCESS
    this.updateItemFalureMessage = UPDATE_ARTICLE_FAILURE
  }

}

export const _instance = new articleAction( window.apiUrl.articles, "article" )



